import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { GeralService } from '../../shared/services/geral/geral';

@Component({
    selector: 'app-visaoGeral-page',
    templateUrl: './visaoGeral.component.html',
    styleUrls: ['./visaoGeral.component.scss'],
    animations: [routerTransition()]
})
export class VisaoGeralPageComponent implements OnInit {
    //Twitter
    public doughnutChartLabels: string[] = [
        'Jair Bolsonaro',
        'João Amoêdo',
        'Marina Silva'
    ];
    public doughnutChartData: number[] = [350, 450, 100];
    public doughnutChartType: string = 'doughnut';


    //Noticias
    public barChartOptions: any = {
        scaleShowVerticalLines: false,
        responsive: true
    };
    public barChartLabels: string[] = [
        'Jair Bolsonaro',
        'João Amoêdo',
        'Marina Silva'
    ];
    public barChartType: string = 'bar';
    public barChartLegend: boolean = true;

    public barChartData: any[] = [
        { data: [65, 59, 80, 81, 56, 55, 40], label: 'Terra' },
        { data: [28, 48, 40, 19, 86, 27, 90], label: 'UOL' },
        { data: [28, 48, 40, 19, 86, 27, 90], label: 'G1' }
    ];
    constructor(private service: GeralService) {
        this.service.geralCount().then(data => {
            console.log(data);
            this.barChartData = [
                { data: [data[1].n, data[4].n, data[7].n], label: 'Terra' },
                { data: [data[2].n, data[5].n, data[8].n], label: 'UOL' },
                { data: [data[0].n, data[3].n, data[6].n], label: 'G1' }
            ];
            this.doughnutChartData = [data[9].n, data[10].n, data[11].n];
        });
    }   
    ngOnInit() { }
}
